              </template>
              <span>Descargar indicador.</span>
            </v-tooltip>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-subtitle>Distribución de los/as investigadores/as.</v-card-subtitle>
      <v-card-text>
        <v-responsive>
          <GChart
            id="charto"
            :data="barChartData"
            :options="barChartOptions"
            type="ColumnChart"
          />
        </v-responsive>
        <v-responsive style="width: 100%;">
          <v-simple-table
            dense
            class="cellheading mt-2"
          >
            <thead>
              <tr class="caption">
                <th rowspan="2">
                  Categoría
                </th>
