            </thead>
            <tbody>
              <tr>
                <td>Profesores Titulares+A</td>
                <td>{{ generoCategoriaDedicacionData.fem.tit.exc }}</td>
                <td>{{ generoCategoriaDedicacionData.fem.tit.sem }}</td>
                <td>{{ generoCategoriaDedicacionData.masc.tit.exc }}</td>
                <td>{{ generoCategoriaDedicacionData.masc.tit.sem }}</td>
              </tr>
              <tr>
                <td>Profesores Asociados+B</td>
                <td>{{ generoCategoriaDedicacionData.fem.aso.exc }}</td>
                <td>{{ generoCategoriaDedicacionData.fem.aso.sem }}</td>
                <td>{{ generoCategoriaDedicacionData.masc.aso.exc }}</td>
                <td>{{ generoCategoriaDedicacionData.masc.aso.sem }}</td>
              </tr>
              <tr>
                <td>Profesores Adjuntos+C</td>
                <td>{{ generoCategoriaDedicacionData.fem.adj.exc }}</td>
                <td>{{ generoCategoriaDedicacionData.fem.adj.sem }}</td>
                <td>{{ generoCategoriaDedicacionData.masc.adj.exc }}</td>
                <td>{{ generoCategoriaDedicacionData.masc.adj.sem }}</td>
              </tr>
              <tr>
                <td>JTP+D1</td>
                <td>{{ generoCategoriaDedicacionData.fem.jtp.exc }}</td>
                <td>{{ generoCategoriaDedicacionData.fem.jtp.sem }}</td>
                <td>{{ generoCategoriaDedicacionData.masc.jtp.exc }}</td>
                <td>{{ generoCategoriaDedicacionData.masc.jtp.sem }}</td>
              </tr>
              <tr>
                <td>Ayudantes de 1ra+D2</td>
                <td>{{ generoCategoriaDedicacionData.fem.ayu.exc }}</td>
                <td>{{ generoCategoriaDedicacionData.fem.ayu.sem }}</td>
                <td>{{ generoCategoriaDedicacionData.masc.ayu.exc }}</td>
                <td>{{ generoCategoriaDedicacionData.masc.ayu.sem }}</td>
              </tr>
            </tbody>
          </v-simple-table>
        </v-responsive>
      </v-card-text>
    </div>
  </v-card>
