<script>
import { mapGetters, mapState } from 'vuex'
import { mdiDownload } from '@mdi/js'
import { colorLuminance } from '@/utils.js'
export default {
  data: () => ({
    mdiDownload
  }),
  computed: {
    ...mapState(['dark', 'colors']),
    ...mapGetters(['investigadoresCargos', 'mobileSize']),
    barChartOptions () {
      return {
        chartArea: {
          left: 40,
          right: 20,
          top: 20,
          bottom: 40
        },
        // tooltip: {
        //   isHtml: true,
        //   textStyle: { color: 'white', bold: false }
        // },
        colors: [this.colors[0], this.colors[1], colorLuminance(this.colors[0], 0.6), colorLuminance(this.colors[1], 0.6)],
        // colors: ['#3306cc', '#a10912', '#3366cc', '#dc3912'],
        title: '',
        backgroundColor: { fill: 'transparent' },
        height: 350,
        // isStacked: 'absolute',
        legend: {
          position: 'top',
          maxLines: 3,
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        },
        bar: { groupWidth: '70%' },
        vAxis: {
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        },
        hAxis: {
          format: '0',
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        }
      }
    },
    barChartData () {
      return {
        cols: [
          { id: 'Franja etária', label: 'Franja etária', type: 'string' },
          { id: 'FemeninoExc', label: 'Fem. Exclusiva', type: 'number' },
          { id: 'MasculinoExc', label: 'Masc. Exclusiva', type: 'number' },
          { id: 'FemeninoSem', label: 'Fem. Semiexclusiva', type: 'number' },
          { id: 'MasculinoSem', label: 'Masc. Semiexclusiva', type: 'number' }
        ],
        rows: [
          {
            c: [
              { v: 65, f: 'Profesores Titulares+A' },
              {
                v:
                  this.generoCategoriaDedicacionData.fem.tit.exc,
                f: this.generoCategoriaDedicacionData.fem.tit.exc
              },
              {
                v: this.generoCategoriaDedicacionData.masc.tit.exc,
                f: this.generoCategoriaDedicacionData.masc.tit.exc
              },
              {
                v:
                    this.generoCategoriaDedicacionData.fem.tit.sem,
                f: this.generoCategoriaDedicacionData.fem.tit.sem
              },
              {
                v: this.generoCategoriaDedicacionData.masc.tit.sem,
                f: this.generoCategoriaDedicacionData.masc.tit.sem
              }
            ]
          },
          {
            c: [
              { v: 65, f: 'Profesores Asociados+B' },
              {
                v:
                  this.generoCategoriaDedicacionData.fem.aso.exc,
                f: this.generoCategoriaDedicacionData.fem.aso.exc
              },
              {
                v: this.generoCategoriaDedicacionData.masc.aso.exc,
                f: this.generoCategoriaDedicacionData.masc.aso.exc
              },
              {
                v:
                    this.generoCategoriaDedicacionData.fem.aso.sem,
                f: this.generoCategoriaDedicacionData.fem.aso.sem
              },
              {
                v: this.generoCategoriaDedicacionData.masc.aso.sem,
                f: this.generoCategoriaDedicacionData.masc.aso.sem
              }
            ]
          },
          {
            c: [
              { v: 65, f: 'Profesores Adjuntos+C' },
              {
                v:
                  this.generoCategoriaDedicacionData.fem.adj.exc,
                f: this.generoCategoriaDedicacionData.fem.adj.exc
              },
              {
                v: this.generoCategoriaDedicacionData.masc.adj.exc,
                f: this.generoCategoriaDedicacionData.masc.adj.exc
              },
              {
                v:
                    this.generoCategoriaDedicacionData.fem.adj.sem,
                f: this.generoCategoriaDedicacionData.fem.adj.sem
              },
              {
                v: this.generoCategoriaDedicacionData.masc.adj.sem,
                f: this.generoCategoriaDedicacionData.masc.adj.sem
              }
            ]
          },
          {
            c: [
              { v: 65, f: 'JTP+D1' },
              {
                v:
                  this.generoCategoriaDedicacionData.fem.jtp.exc,
                f: this.generoCategoriaDedicacionData.fem.jtp.exc
              },
              {
                v: this.generoCategoriaDedicacionData.masc.jtp.exc,
                f: this.generoCategoriaDedicacionData.masc.jtp.exc
              },
              {
                v:
                    this.generoCategoriaDedicacionData.fem.jtp.sem,
                f: this.generoCategoriaDedicacionData.fem.jtp.sem
              },
              {
                v: this.generoCategoriaDedicacionData.masc.jtp.sem,
                f: this.generoCategoriaDedicacionData.masc.jtp.sem
              }
            ]
          },
          {
            c: [
              { v: 65, f: 'Ayudantes de 1ra+D2' },
              {
                v:
                  this.generoCategoriaDedicacionData.fem.ayu.exc,
                f: this.generoCategoriaDedicacionData.fem.ayu.exc
              },
              {
                v: this.generoCategoriaDedicacionData.masc.ayu.exc,
                f: this.generoCategoriaDedicacionData.masc.ayu.exc
              },
              {
                v:
                    this.generoCategoriaDedicacionData.fem.ayu.sem,
                f: this.generoCategoriaDedicacionData.fem.ayu.sem
              },
              {
                v: this.generoCategoriaDedicacionData.masc.ayu.sem,
                f: this.generoCategoriaDedicacionData.masc.ayu.sem
              }
            ]
          }
        ]
      }
    },
    generoCategoriaDedicacionData () {
      return this.investigadoresCargos.reduce((acc, cur) => {
        const nivA = new RegExp('Niv A', 'gm')
        const nivB = new RegExp('Niv B', 'gm')
        const nivC = new RegExp('Niv C', 'gm')
        const nivD1 = new RegExp('Niv D1', 'gm')
        const nivD2 = new RegExp('Niv D2', 'gm')
        // const profesores = nivA.test(cur.descripcion_categoria) ||
        //   nivB.test(cur.descripcion_categoria) ||
        //   nivC.test(cur.descripcion_categoria) ||
        //   cur.descripcion_categoria === 'Profesor Asociado' ||
        //   cur.descripcion_categoria === 'ASOCIADO SIMPLE' ||
        //   cur.descripcion_categoria === 'Profesor Titular' ||
        //   cur.descripcion_categoria === 'Profesor Adjunto' ||
        //   cur.descripcion_categoria === 'ADJUNTO SIMPLE'
        const titular = nivA.test(cur.descripcion_categoria) ||
          cur.descripcion_categoria === 'Profesor Titular'
        const asociado = nivB.test(cur.descripcion_categoria) ||
          cur.descripcion_categoria === 'Profesor Asociado' ||
          cur.descripcion_categoria === 'ASOCIADO SIMPLE'
        const adjunto = nivC.test(cur.descripcion_categoria) ||
          cur.descripcion_categoria === 'Profesor Adjunto' ||
          cur.descripcion_categoria === 'ADJUNTO SIMPLE'
        const d1jtp = nivD1.test(cur.descripcion_categoria) ||
          cur.descripcion_categoria === 'Jefe Trab. Practicos' ||
          cur.descripcion_categoria === 'Jef Trab Pract NM' ||
          cur.descripcion_categoria === 'JTP SIMPLE'
        const d2ayu = nivD2.test(cur.descripcion_categoria) ||
          cur.descripcion_categoria === 'Ayudante de 1ra.' ||
          cur.descripcion_categoria === 'AYUDANTE DE 1º SIMPL'
        const exclusiva = cur.descripcion_dedicacion === 'Exclusiva'
        const semiexclusiva = cur.descripcion_dedicacion === 'Semi-Exclusiva' ||
          cur.descripcion_dedicacion === 'Semi - Excl Conven'
        const femenino = cur.sexo === 'F'
        const masculino = cur.sexo === 'M'
        return femenino
          ? {
            masc: acc.masc,
            fem: titular
              ? { ...acc.fem, tit: { exc: acc.fem.tit.exc + (exclusiva ? 1 : 0), sem: acc.fem.tit.sem + (semiexclusiva ? 1 : 0) } }
              : asociado
                ? { ...acc.fem, aso: { exc: acc.fem.aso.exc + (exclusiva ? 1 : 0), sem: acc.fem.aso.sem + (semiexclusiva ? 1 : 0) } }
                : adjunto
                  ? { ...acc.fem, adj: { exc: acc.fem.adj.exc + (exclusiva ? 1 : 0), sem: acc.fem.adj.sem + (semiexclusiva ? 1 : 0) } }
                  : d1jtp
                    ? { ...acc.fem, jtp: { exc: acc.fem.jtp.exc + (exclusiva ? 1 : 0), sem: acc.fem.jtp.sem + (semiexclusiva ? 1 : 0) } }
                    : d2ayu
                      ? { ...acc.fem, ayu: { exc: acc.fem.ayu.exc + (exclusiva ? 1 : 0), sem: acc.fem.ayu.sem + (semiexclusiva ? 1 : 0) } }
                      : acc.fem
          }
          : masculino
            ? {
              fem: acc.fem,
              masc: titular
                ? { ...acc.masc, tit: { exc: acc.masc.tit.exc + (exclusiva ? 1 : 0), sem: acc.masc.tit.sem + (semiexclusiva ? 1 : 0) } }
                : asociado
                  ? { ...acc.masc, aso: { exc: acc.masc.aso.exc + (exclusiva ? 1 : 0), sem: acc.masc.aso.sem + (semiexclusiva ? 1 : 0) } }
                  : adjunto
                    ? { ...acc.masc, adj: { exc: acc.masc.adj.exc + (exclusiva ? 1 : 0), sem: acc.masc.adj.sem + (semiexclusiva ? 1 : 0) } }
                    : d1jtp
                      ? { ...acc.masc, jtp: { exc: acc.masc.jtp.exc + (exclusiva ? 1 : 0), sem: acc.masc.jtp.sem + (semiexclusiva ? 1 : 0) } }
                      : d2ayu
                        ? { ...acc.masc, ayu: { exc: acc.masc.ayu.exc + (exclusiva ? 1 : 0), sem: acc.masc.ayu.sem + (semiexclusiva ? 1 : 0) } }
                        : acc.masc
            }
            : acc
      },
      {
        fem: {
          tit: {
            exc: 0,
            sem: 0
          },
          aso: {
            exc: 0,
            sem: 0
          },
          adj: {
            exc: 0,
            sem: 0
          },
          jtp: {
            exc: 0,
            sem: 0
          },
          ayu: {
            exc: 0,
            sem: 0
          }
        },
        masc: {
          tit: {
            exc: 0,
            sem: 0
          },
          aso: {
            exc: 0,
            sem: 0
          },
          adj: {
            exc: 0,
            sem: 0
          },
          jtp: {
            exc: 0,
            sem: 0
          },
          ayu: {
            exc: 0,
            sem: 0
          }
        }
      })
    }
  }
}
</script>
